.big-posters-parent {
    padding: 0 10px;
}

.big-posters-heading {
    margin-left: 7px;
    margin-top: 25px;
}

.big-posters-heading::after {
    content: ' ';
    background-image: url(../../assets//images/next.svg);
    width: 10px;
    height: 10px;
    background-size: cover;
    background-position: center;
    display: inline-block;
    margin-left: 10px;
}

.big-posters-subheading {
    margin-left: 7px;
    margin-top: 10px;
    font-family: Inter-BETA;
    font-size: 14px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.43;
    letter-spacing: normal;
    color: #f8f8f8;
}

.big-posters {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    padding: 10px 0;
}

.big-posters::-webkit-scrollbar {
    display: none;
}

.big-posters {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

@media (max-width:769px) {
    .big-posters {
        display: flex;
        flex-direction: row;
        overflow-x: auto;
        /* will-change: transform; */
    }
}