:root {
    --carousel-height-calc_value: 1.78;
}

.carousel-slide-container {
    width: 100%;
    height: calc(100vw / var(--carousel-height-calc_value));
    /* background: url(../../assets/images/slide-image.jpg) center center / cover no-repeat rgb(97, 94, 94); */
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* padding: 120px; */
    opacity: 0;
}

.carousel-slide-details-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 40px;
    height: 100%;
    background: linear-gradient(90deg, rgb(0, 0, 0), transparent, transparent);
}

.carousel-slide-feature-text {
    padding: 4px 10px;
    background: linear-gradient(to right, var(--primary-background-color), #3f0949);
    display: inline-block;
    color: #ffffff;
    border-radius: 7px;
    font-size: 12px;
    margin-bottom: 20px;
}

.carousel-slide-title {
    font-family: INTER;
    font-size: 38px;
    color: #ffffff;
    text-shadow: 0 2px 2px #000000;
}

.carousel-slide-feature-list-container ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-evenly;
}

.carousel-slide-feature-list-container ul li {
    color: #ffffff;
    font-size: 12px;
    font-weight: normal;
}

.carousel-slide-feature-list-container ul li::after {
    content: '\2022';
    width: 3px;
    height: 3px;
    color: #ffffff;
    margin: auto 10px;
}

.carousel-slide-feature-list-container ul li:last-child::after {
    content: '';
    margin-right: 0px;
}

.carousel-slide-play-icon-container svg {
    width: 20px;
    margin: 0px 10px;
}

.carousel-slide-play-icon-container svg path {
    fill: #ffffff;
    fill-rule: evenodd;
    stroke-width: 15px;
    stroke: white;
}

.carousel-slide-action-container {
    display: flex;
    margin: 20px 0;
}

.carousel-slide-action-container>div {
    margin-right: 18px;
}

.carousel-slide-cta {
    display: flex;
    font-size: 15px;
    padding: 12px 20px;
}

.carousel-slide-feature-list-container {
    display: flex;
    align-items: center;
}

.carousel-slide-age-rating {
    display: inline;
    padding: 1px;
    background: rgb(42 42 42 / 85%);
    border-radius: 4px;
    font-size: 12px;
    margin-right: 10px;
}

.carousel-slide-secondary-icon {
    padding: 12px 14px;
}

.carousel-slide-description {
    width: 350px;
    font-size: 14px;
    color: rgb(255 255 255 / 0.70);
    font-family: sans-serif;
    font-weight: normal;
    line-height: 1.5;
    display: none;
}


/* ========= RESPONSIVE ========== */

@media only screen and (max-width: 769px) {
    .carousel-slide-container {
        background-size: 100% auto;
        /* top: 50px; */
        justify-content: flex-end;
    }
    .carousel-slide-details-container {
        padding: 0;
        align-items: center;
        background: linear-gradient(360deg, rgb(0 0 0), rgb(193 255 0 / 0));
    }
    .carousel-slide-title {
        font-size: 24px;
    }
    .carousel-slide-feature-text {
        border-radius: 2px;
        font-size: 8px;
        align-self: flex-start;
    }
    .carousel-slide-description {
        display: none;
    }
}